.form-card-1
{
    margin:15px;
    box-shadow:0px 3px 12px rgba(0,0,0,0.25);
    padding:15px;
    width:90%;
    height:850px;
    text-align:center;
    justify-content:center;
    align-items:center;
    border-radius:15px;
}

.text-form-1
{
    padding:15px;
    border-radius:2px;
    border:solid 1px #fd8a02;
    margin:5px;
    display:flex;
    width:375px;
    font:0.75em Century Gothic;
}


.select-form-1
{
    padding:15px;
    border-radius:10px;
    border:solid 1px #fd8a02;
    margin:5px;
    margin-top:15px;
    display:flex;
    width:410px;
    font:0.75em Century Gothic;
}

h2 {  font:2em Century Gothic; }
h4 {  font:1.2em Century Gothic; }

.btn-type-1
{
    border:none;
    background:#fd8a02;
    color:#fff;
    padding:20px;
    margin:10px;
    border-radius:10px;
    width:200px;
    border:solid 1px #fff;
    text-align:center;
    font:bold 1em Century Gothic;
    cursor:pointer;
}

.btn-type-1:hover
{
    -webkit-transition:250ms ease-in;
    background:#fff;
    border:solid 1px #fd8a02;
    color:#fd8a02;

}